export default {
	data() {
		return {
			userInfo: {},
			// name: '生命如花，有着它特有的活力与规律，只有用心灵去感悟，才能真正地触摸到它的最深处。',
			userObj: {
				name1: '生活，',
				name2: '可以简单点',
				name3: '世界，',
				name4: '可以广阔些',
			},
			infoTabs: [
			// {
			// 	url: '../../pages/index/update-info',
			// 	className: 'person-info',
			// 	name: '修改信息',
			// }, 
			{
				// url: '../../pages/sub-info/index?subType=0',
				url: '../../pages/sub-info/teachers',
				className: 'person-sub',
