				className: 'team-sub',
				name: '团体报名',
			}],
			moment,
		}
	},
	
	created() {
		this.userInfo = uni.getStorageSync("userinfo")
	},
	
	methods: {
		colorHash(index) {
		    return `avatar-color-${this.simpleHash(index)}`;
		},
		simpleHash(key, mod = 8) {
			return Math.abs(key % mod);
		},
		saveStudent() {
			uni.redirectTo({
				url: '../../pages/index/update-info'
			})
		},
		tabChange(tab) {
			uni.redirectTo({
				url: tab.url
			})
		},
	}
}
